/* General Reset */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

/* Header */
h1 {
  text-align: center;
  margin: 20px 0;
}

/* Grid Section */
.company-grid {
  display: flex;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
  padding: 10px;
}

.company-card {
  border: 1px solid #333;
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  text-transform: capitalize;
  border-radius: 5px;
}

.company-card:hover {
  background-color: #ddd;
  transform: scale(1.05);
}

.company-card.selected {
  background-color: #4285f4;
  color: white;
  font-weight: bold;
}

/* Pagination Section */
.pagination-container {
  text-align: center;
  margin: 10px 0;
}

.pagination-btn {
  padding: 5px 10px;
  cursor: pointer;
  margin: 0 5px;
  border: none;
  background-color: #4285f4;
  color: white;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.pagination-btn:hover:not(:disabled) {
  background-color: #357ae8;
}

.pagination-btn:disabled {
  background-color: #999;
  cursor: not-allowed;
}

/* Selected Company Section */
.selected-company-container {
  text-align: center;
  margin: 20px 0;
}

.company-logo {
  max-width: 150px;
  height: auto;
  margin: 10px 0;
  border: 2px solid #ddd;
  border-radius: 5px;
}

/* Question Cards */
.question-container {
  margin: 20px auto;
  max-width: 600px;
}

.question-card {
  border: 1px solid #333;
  padding: 10px;
  margin: 5px 0;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.question-card:hover {
  background-color: black;
  color: white;
  transform: scale(1.02);
}

.difficulty {
  font-weight: bold;
  margin-right: 10px;
  padding: 3px 7px;
  border-radius: 3px;
  text-transform: capitalize;
}

.difficulty.easy {
  background-color: #28a745;
  color: red;
}

.difficulty.medium {
  background-color: #ffc107;
  color: white;
}

.difficulty.hard {
  background-color: #dc3545;
  color: orange;
}

/* Responsive Design */
@media (max-width: 600px) {
  .company-grid {
    flex-direction: column;
    align-items: center;
  }

  .question-container {
    padding: 0 10px;
  }
}
