    /* Container styles */
    .mainContainer {
        background-color: #060b30;
        width: 100vw;
    }

    .sub-Container {
        display: flex;
        flex-direction: row;
    }

    /* Sidebar styles */

    .image-container {
        display: flex;
        align-items: center;
        padding: 10px;
    }

    .imageIcon {
        border-radius: 50%;
        height: 70px;
        width: 70px;
        margin: 5px;
        background-color: transparent;
    }

    .details {
        margin-left: 15px;
        padding-top: 15px;
    }

    .headingName {
        color: #ffffff;
        font-size: 18px;
    }

    .view-more {
        color: blue;
        font-size: 13px;
        cursor: pointer;
    }

    /* List items and button styles */
    .listItems {
        width: 100%;
        padding: 10px 0;
        font-size: 30px;
    }

    .item {
        display: flex;
        align-items: center;
        width: 100%;
        padding: 10px;
        margin: 5px 0;
        border-radius: 5px;
        text-align: center;
        cursor: pointer;
        transition: background-color 0.1s ease;
    }

    .item:hover,
    .item.active {
        background-color: #1d2a52;
    }

    .icon-course {
        height: 28px;
        width: 28px;
        color: #cccccc;
    }

    .paraEle1 {
        margin-left: 12px;
        font-size: 18px;
        padding-top: 2px;
        color: #dcdcdc;
    }

    /* Divider and general styles */
    .h-line {
        border: 0;
        height: 1px;
        background: #ffffff;
        margin: 3px;
    }
    .image{
        /* background-image: url("https://app.rigi.club/wp-content/uploads/2022/09/How-create-and-sell-online-courses-for-free.png"); */
        width: 100%;
        background-size: cover;
    }

    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    .copy-right{
        font-family: cursive;
        font-size: 12px;
    }
    /* Sidebar visibility */
    
   
    
    /* General adjustments to maintain responsiveness */
    .main-content {
        width: 100%;
        padding: 15px;
    }
    /* Sidebar styles */
    .left-sideContainer {
        position: fixed; /* Fixes the sidebar */
        top: 0; /* Aligns it to the top */
        left: 0; /* Aligns it to the left */
        background-color: #070a1a;
        width: 16%;
        height: 100vh;
        border-radius: 8px;
        color: aliceblue;
        padding: 15px;
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 100; /* Ensures it stays above other content */
      /* Allows scrolling within the sidebar if content overflows */
    }

    /* Adjust main content to avoid overlapping with fixed sidebar */
    .mainContainer {
        margin-left: 16%; /* Matches the sidebar width */
        background-color: #060b30;
        width: 90vw;
    }

    /* Hamburger menu for responsive design */

    /* Responsive adjustments */
    @media screen and (max-width: 768px) {
        .left-sideContainer {
            width: 70%; /* Adjust sidebar width for smaller screens */
        }

        .mainContainer {
            margin-left: 0;
            width: 100vw;
        }

    }
    .blurred {
        opacity: 0.5;
        pointer-events: none;
      }
      
      .blurred:hover {
        cursor: not-allowed;
      }
      
      .item {
        display: flex;
        align-items: center;
        padding: 10px;
        margin: 5px 0;
        cursor: pointer;
        transition: all 0.3s ease;
      }
      
      
     
    