/* Main Container */
.login-container {
  display: flex;
  min-height: 100vh;
  width: 100%;
  justify-content: center;
  background: linear-gradient(135deg, #0d0d27, #070a1a);
}

/* Wrapper for Login */
.login-wrapper {
  width: 100%;
  max-width: 400px;
  perspective: 1000px;
  position: relative
}

/* Flip Effect for Card */
.login-wrapper.flipped {
  transform: rotateY(180deg);
}

/* Sections for Flip Animation */
.signup-section,
.login-section {
  backface-visibility: hidden;
  position: absolute;
  width: 100%;
  padding: 20px;
  transition: transform 0.6s ease-in-out;
}

/* Signup Section: Show and Hide */
.signup-section.show-form {
  transform: rotateY(0deg);
}

.signup-section.hide-form {
  transform: rotateY(-180deg);
}

/* Login Section: Show and Hide */
.login-section.show-form {
  transform: rotateY(0deg);
}

.login-section.hide-form {
  transform: rotateY(180deg);
}

/* Title and Subtitle */
.login-title, .signup-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #ffffff;
}

.login-subtitle, .signup-subtitle {
  font-size: 16px;
  color: #cccccc;
  margin-bottom: 30px;
  text-align: center;
}

/* Form Styles */
.login-form, .signup-form {
  width: 100%;
}

/* Input Field Styles */
.form-group {
  margin-bottom: 20px;
}

.form-label {
  font-size: 14px;
  color: #cccccc;
  margin-bottom: 8px;
  display: block;
}

.form-input {
  width: 100%;
  padding: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.05);
  color: #ffffff;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s ease;
}

.form-input:focus {
  border-color: #904bcc;
}

/* Buttons */
.login-button, .signup-button {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  font-weight: bold;
  background: linear-gradient(90deg, orange, white);
  color: #ffffff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.login-button:hover, .signup-button:hover {
  background: linear-gradient(90deg, #904bcc, #4caf50);
}

/* Toggle Link Styles */
.signup-link, .login-link {
  margin-top: 15px;
  font-size: 14px;
  color: #cccccc;
  text-align: center;
}

.signup-link a, .login-link a {
  color: #904bcc;
  text-decoration: none;
  font-weight: bold;
}

.signup-link a:hover, .login-link a:hover {
  color: #ffffff;
}

/* Error Message Styles */
.error-message {
  align-self: start;
  font-size: 12px;
  margin-top: 3px;
  margin-bottom: 0px;
  line-height: 16px;
  color: #ff0b37;
}

/* Password Input Wrapper */
.password-input-wrapper {
  display: flex;
  align-items: center;
  position: relative;
}

.password-toggle-btn {
  position: absolute;
  right: 10px;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 18px;
  color: #cccccc;
  transition: color 0.3s ease;
}

.password-toggle-btn:hover {
  color: #904bcc;
}

.password-toggle-btn:focus {
  outline: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  .login-wrapper {
    padding: 30px;
  }

  .login-title, .signup-title {
    font-size: 22px;
  }

  .login-subtitle, .signup-subtitle {
    font-size: 14px;
  }

  .form-input, .login-button, .signup-button {
    font-size: 14px;
  }
}

/* Link Button for Toggle */
.link-button {
  border-radius: 8px;
  color: #ffffff;
  background-color: transparent;
  font-size: 12px;
  font-weight: bold;
  padding: 2px;
  border: none;
  outline: none;
  text-decoration: underline;
  cursor: pointer;
  transition: color 0.3s ease;
}

.link-button:hover {
  color: green;
