/* Header Container */
.header-container1 {
    width: 100%;
    height: 10%;
    background-color: #0d0d27;
    color: #ffffff;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    position: relative;
    z-index: 1000;
}

/* Logo Text Styles */
.logo-text {
    font-size: 28px;
    font-weight: 800;
    background: linear-gradient(to right, orange, white, green);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-transform: uppercase;
    letter-spacing: 3px;
    cursor: pointer;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-shadow: none;
    transition: transform 0.3s ease;
}

/* Hover Effect */
.logo-text:hover {
    transform: translateY(-2px);
    text-shadow: 0px 2px 8px rgba(255, 165, 0, 0.6);
}

/* Navigation Items */
.items-container2 {
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: center;
}

.paraEle2 {
    text-decoration: none;
    color: #ffffff;
    font-size: 16px;
    font-weight: bold;
    transition: color 0.3s ease;
}

.paraEle2:hover {
    color: #904bcc;
}

/* Login Button */
.login-button1 {
    background-color: #904bcc;
    color: #ffffff;
    height: 40px;
    width: 130px;
    border-radius: 10px;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 15px;
    font-weight: bold;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.login-button1:hover {
    background-color: #722ea6;
    transform: scale(1.05);
}

/* Hamburger Icon */
.hamburger-icon {
    display: none;
    font-size: 25px;
    cursor: pointer;
    color: white;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
    .items-container2 {
        display: none; /* Initially hide the menu items */
        flex-direction: column;
        position: absolute;
        top: 60px;
        right: 20px;
        background-color: #0d0d27;
        border-radius: 10px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
        width: 200px;
        z-index: 999;
    }

    .items-container2.active {
        display: flex; /* Show menu items when active */
    }

    .hamburger-icon {
        display: block; /* Show hamburger icon on smaller screens */
    }

    .login-button1 {
        width: 100%;
    }
}
