/* General Container Styling */
.course-container {
  padding: 20px;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  color: #ffffff;
}

/* Heading Style */
.course-heading {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  font-weight: bold;
  color: #ffffff;
}

/* Filter Section */
.category-filter {
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.category-filter label {
  font-size: 1rem;
}

.category-filter select {
  padding: 8px 10px;
  font-size: 1rem;
  border-radius: 4px;
  border: 1px solid #444;
  background-color: #2a2a3f;
  color: #ffffff;
}

.category-filter select:focus {
  outline: none;
  border-color: #f37254;
}

/* Course List */
.course-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

/* Course Card */
.course-card1 {
  background-color: #2a2a3f;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  width: 320px;
  transition: transform 0.2s ease-in-out;
  position: relative;
}

.course-card:hover {
  transform: scale(1.05);
}

.course-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.course-content {
  padding: 15px;
}

.course-title1 {
  font-size: 19px;
  font-weight: bold;
  margin: 10px 0;
  color: #ffffff;
  text-align: center;

}

.course-meta {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
  margin-bottom: 8px;
  color: #ffffff;
}
.course-language{
  font-weight: bold;
}
.course-description1 {
  font-size: 0.9rem;
  margin: 10px 0;
  line-height: 1.4;
  color: #ffffff;
}

.course-price {
  font-size: 1.5rem;
  margin: 10px 0;
  color: green;
  text-align: left;
  font-weight: bold;
}

/* Badge Styles */
.badge {
  position: absolute;
  top: 15px;
  left: 15px;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 0.85rem;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
}

.free {
  background-color: #4caf50;
}

.premium {
  background-color: #f37254;
}

/* Button Styles */
.enroll-button {
  background-color: #f37254;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 10px 15px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: bold;
  transition: background-color 0.3s ease;
  width: 100%;
  margin-top: 10px;
}

.enroll-button:hover {
  background-color: #d65a3f;
}

/* Pagination */
.pagination-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.pagination-button {
  padding: 10px 15px;
  background-color: #f37254;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  margin: 0 5px;
}

.pagination-button:disabled {
  background-color: #444;
  cursor: not-allowed;
}

.page-info {
  font-size: 1rem;
  color: #ffffff;
  margin: 0 10px;
}

/* Loader */
.loader {
  border: 4px solid #f3f3f3;
  border-radius: 50%;
  border-top: 4px solid #f37254;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin: 50px auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.course-duration{
  margin-left: 5px;
  font-weight: bold;
  color: #f37254;
  font-size: 15px;
}