/* Basic Shine Bharat Theme for the Stories Section */
.stories-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #333;
    padding: 20px;
    border-radius: 10px;
    width: 180%;
    margin: 0 auto;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden; /* Prevents horizontal overflow */
  }
  
  .shine-bharat-heading {
    text-align: center;
    font-size: 2rem;
    color: #f39c12;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .stories-scroll-container {
    display: flex;
    flex-wrap: nowrap; /* Prevents items from wrapping */
    overflow-x: hidden; /* Hide overflow for smooth scroll */
    padding-bottom: 20px; /* Ensures space for scrollbar */
    gap: 20px;
    animation: scrollStories 20s linear infinite;
  }
  
  .story-item {
    width: 100%; /* Reduced the width of each story item */
    background-color: #fff;
    height: 100%;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    flex-shrink: 1; /* Prevents shrinking */
  }
  
  @keyframes scrollStories {
    0% {
      transform: translateX(100%); /* Start from the right */
    }
    100% {
      transform: translateX(-100%); /* Move to the left */
    }
  }
  
  .founder-name1 {
    font-size: 1.5rem;
    color: #2c3e50;
    margin-bottom: 10px;
  }
  
  .founder-story {
    font-size: 1rem;
    font-style: italic;
    color: #7f8c8d;
    margin-bottom: 10px;
  }
  
  .founder-company {
    font-size: 1.1rem;
    font-weight: bold;
    color: #3498db;
    text-align: right;
  }
  