/* Founders Section Container */
.founders-section {
  padding: 60px 20px;
  background-color: #060b30; /* Matches the theme */
  text-align: center;
}

/* Section Title */
.section-title {
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 40px;
}

/* Founders Grid */
.founders-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  justify-items: center;
}

/* Founder Card */
.founder-card {
  position: relative;
  background-color: #121212;
  border-radius: 15px;
  width: 260px;
  padding: 20px;
  text-align: center;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  color: #ffffff;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.founder-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
}

/* LinkedIn Icon */
.linkedin-icon {
  position: absolute;
  top: 10px;
  right: 10px;
  /* background-color: #ffffff; */
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

.linkedin-icon img {
  width: 20px;
  height: 20px;
}

/* Founder Image */
.founder-image {
  width: 180px;
  height: 250px;
 border-radius: 20%;
  object-fit: cover;
  margin-bottom: 15px;
  border: 3px solid #0078d4;
}

/* Founder Name */
.founder-name {
  font-size: 1.2rem;
  font-weight: bold;
  color: #ffffff;
  margin-bottom: 5px;
}

/* Founder Role */
.founder-role {
  font-size: 1rem;
  color: #9ca3af; /* Light gray for subtlety */
  margin-bottom: 5px;
}

/* Founder Designation */
.founder-designation {
  font-size: 0.9rem;
  color: #6b7280; /* Even lighter gray for distinction */
}
