/* Footer Container */
.footer-container-modern {
    width: 100%;
    background-color: #0d0d27;
    color: #ffffff;
    padding: 40px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Footer Grid Layout */
.footer-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1200px;
    width: 100%;
    gap: 20px;
    margin-bottom: 20px;
}

/* Footer Section */
.footer-section-modern {
    flex: 1;
    min-width: 250px;
    text-align: left;
}

/* Titles and Subtitles */
.footer-title {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 10px;
    background: linear-gradient(to right, orange, white, green);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.footer-subtitle {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 10px;
    color: #904bcc;
}

/* Text and Descriptions */
.footer-description {
    font-size: 14px;
    line-height: 1.6;
    margin-bottom: 15px;
    color: #d1d1d1;
}

/* Footer Links */
.footer-list-modern {
    list-style: none;
    padding: 0;
}

.footer-list-modern li {
    margin-bottom: 8px;
}

.footer-link-modern {
    text-decoration: none;
    color: #ffffff;
    font-size: 14px;
    transition: color 0.3s ease, transform 0.2s ease;
}

.footer-link-modern:hover {
    color: #904bcc;
    transform: translateX(5px);
}

/* Social Icons */
.footer-socials {
    display: flex;
    gap: 15px;
}

.footer-social-icon {
    font-size: 20px;
    color: #ffffff;
    transition: color 0.3s ease, transform 0.2s ease;
}

.footer-social-icon:hover {
    color: #904bcc;
    transform: scale(1.2);
}

/* Footer Bottom */
.footer-bottom {
    width: 100%;
    border-top: 1px solid #904bcc;
    padding-top: 20px;
    text-align: center;
}

.footer-bottom-text {
    font-size: 14px;
    color: #d1d1d1;
}

/* Responsive Design */
@media (max-width: 768px) {
    .footer-grid {
        flex-direction: column;
        align-items: center;
    }

    .footer-section-modern {
        text-align: center;
    }

    .footer-socials {
        justify-content: center;
    }
}
